.modal {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 98;

  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  background-color: rgba(88, 88, 88, 0.6);
  backdrop-filter: blur(6px);
  visibility: visible;
  opacity: 1;

  -ms-overflow-style: none;
  scrollbar-width: none;

  position: fixed;
  transform: scale(1);

  &::-webkit-scrollbar {
    display: none;
  }

  &__wrapper {
    position: relative;

    display: flex;
    flex-shrink: 0;
    justify-content: center;
    width: 100%;
    padding: 60px 40px;
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    cursor: pointer;
  }

  &__content {
    position: relative;

    width: 520px;
    padding: 20px 0 32px;
    padding-bottom: 24px;

    overflow: hidden;

    background-color: $color-default-white;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
    border-radius: 10px;
  }

  &__close-btn {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
  }

  &.is-active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;
    background-color: rgba(88, 88, 88, 0.6);

    pointer-events: auto;
  }

  form {
    display: flex;
    flex-wrap: wrap;
  }

  &__description {
    margin: 0;
    margin-bottom: 20px;
    width: 100%;
    font-size: 24px;
    line-height: 36px;
    font-weight: 700;
    text-align: center;
    letter-spacing: 0.4px;
  }
}

.modal__submit {
  margin: 0 auto;
  display: block;
}

.modal__validation-message {
  display: flex;
  align-items: center;
  line-height: 20px;
  margin: 0 auto;
  margin-top: 12px;
  width: 100%;
  justify-content: center;

  &--error {
    color: $color-burnt-sienna;
  }

  &--success {
    color: $color-light-sea-green;
    margin-top: 16px;
  }
}

.modal__container .modal__validation-message {
  svg {
    margin-top: 0;
    margin-right: 10px;
    width: 20px;
    height: 20px;
  }
}

.modal__container-svg {
  margin-top: 0;
  margin-right: 10px;
  width: 20px;
  height: 20px;
}

.modal__container {
  padding: 0 20px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  border: 0;
  border-radius: 0 0 12px 12px;
  position: relative;

  svg {
    display: block;
    width: 14px;
    height: 12px;
    margin-top: 55px;
    fill: none;
  }

  .modal__input-wrapper {
    width: 100%;
  }

  .custom-input {
    width: 100%;
  }

  .modal__input-wrapper--min {
    width: 220px;
  }
}

.modal__container:last-child {
  &::after {
    display: none;
  }
}

.modal__select-wrapper {
  padding-top: 17px;
}
