// не нужно добавлять свои миксины для ресета списков, обнуления кнопок и прочие
// этим вы усложняете работу команды, проверку вашего кода и будущую поддержку проекта

@mixin dotted-line {
  content: "";
  left: 0;
  position: absolute;
  display: block;
  height: 1px;
  width: 520px;
  background: linear-gradient(to right, transparent 80%, $color-echo-blue 20%), linear-gradient(to right, transparent, transparent);
  background-size: 5px 1px, 100% 1px;
}
