.modal-sell {
  width: 100%;

  .modal__input-wrapper--decorated {
    padding-top: 10px;
    margin-top: -5px;
    position: relative;
    padding-bottom: 15px;

    &::after {
      @include dotted-line;
      bottom: -5px;
      left: -20px;
    }
  }

  .modal__select-wrapper {
    margin-bottom: -4px;
  }

  .modal__submit {
    margin-top: 40px;
  }
}
