.footer {
  padding: 12px 0;

  background-color: $color-default-white;

  .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__copyright {
    font-size: 14px;
    color: $color-echo-blue;
    margin: 0;
  }

  &__logo {
    svg {
      width: 92px;
      height: 36px;
    }
  }
}
