.link {
  background-color: transparent;
  color: $color-san-marino;
  font-size: 16px;
  font-weight: 600;
  padding: 0;

  transition: color $trans-default;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      color: $color-light-sea-green-2;
    }

    &:active {
      color: $color-elm;
    }
  }
}
