.modal-buy {
  width: 100%;

  .modal__input-wrapper--decorated {
    position: relative;
    margin-bottom: 15px;
    padding-top: 10px;
    margin-top: -5px;

    &::after {
      @include dotted-line;
      bottom: -20px;
      left: -20px;
    }
  }

  .modal__submit {
    margin-top: 45px;
  }

  .modal__container--small {
    &::after {
      @include dotted-line;
      bottom: -15px;
    }
  }
}
