.close-btn {
  cursor: pointer;
  padding: 0;
  background-color: transparent;
  border: none;

  margin: 0;
  width: 24px;
  height: 24px;

  svg {
    width: 8px;
    height: 8px;
    stroke: $color-grey;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover {
      svg {
        stroke: $color-zambezi;
      }
    }
  }
}
