.user-profile {
  display: flex;
  flex-direction: row;
  font-size: 14px;

  &__cash,
  &__name {
    display: flex;
    align-items: center;

    svg {
      width: 20px;
      height: 20px;
      margin-right: 11px;
    }
  }

  &__cash {
    color: $color-echo-blue;
    letter-spacing: 1px;
    padding-left: 0;
    list-style: none;
    margin: 0;
  }

  &__cash-item {
    display: flex;
    align-items: center;
    margin-left: 40px;
  }

  &__name {
    color: $color-zambezi;
    margin-left: 40px;
  }
}
