.users-list {
  width: 100%;
  user-select: none;
  overflow: hidden;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
}

.users-list__table {
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
  width: 100%;
  border-collapse: collapse;
  border-top: 1px solid $color-solitude;
  overflow: hidden;

  .users-list__table-row {
    display: flex;
    width: 100%;

    &:last-child {
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
    }
  }

  .users-list__table-row .users-list__table-cell:last-child {
    margin-left: auto;
  }

  .users-list__table-head .users-list__table-row {
    background-color: $color-default-white;
  }

  .users-list__table-body .users-list__table-row:nth-child(1n) {
    background-color: $color-alice-blue;
  }

  .users-list__table-body .users-list__table-row:nth-child(2n) {
    background-color: $color-default-white;
  }

  .users-list__table-head .users-list__table-cell {
    text-align: left;
    padding: 19px 20px 20px;
    color: $color-echo-blue;
    font-size: 12px;
    line-height: 14.4px;
    font-weight: 600;
    letter-spacing: 0.3px;
    display: flex;
    flex-wrap: wrap;
    word-break: break-all;
  }

  .users-list__table-body .users-list__table-cell {
    padding: 21px 20px 19px;
    line-height: 20px;
    font-weight: 400;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    word-break: break-all;

    span {
      width: 140px;
      flex-wrap: wrap;
      display: flex;
    }
  }

  .users-list__table-body .users-list__table-name {
    font-weight: 600;
  }

  .users-list__table-head .users-list__table-cell:first-child,
  .users-list__table-body .users-list__table-cell:first-child {
    padding-left: 40px;
    padding-right: 40px;
  }

  .users-list__table-head .users-list__table-cell:last-child,
  .users-list__table-body .users-list__table-cell:last-child {
    padding-left: 40px;
    padding-right: 0;
  }
}

.users-list__table-currency {
  width: 130px;
}

.users-list__table-body .users-list__table-cell ~ .users-list__table-currency {
  text-transform: uppercase;
}

.users-list__table-exchangerate {
  width: 130px;
}

.users-list__table-cashlimit {
  width: 270px;
}

.users-list__table-payments {
  width: 383px;
}

.users-list__table-name {
  align-items: center;
  width: 250px;
  display: flex;

  svg {
    width: 20px;
    height: 20px;
    margin-right: 10px;
  }
}

.users-list__badges-list {
  padding-left: 0;
  display: flex;
  margin: 0;
  list-style: none;
  justify-content: flex-start;
  flex-wrap: wrap;
  align-items: baseline;
  margin-left: -10px;
  margin-top: -10px;
}

.users-list__badges-item {
  margin-left: 10px;
  margin-top: 10px;
  max-width: 300px;
  display: flex;
  flex-wrap: wrap;
  word-break: break-all;

  &:not(:last-child) {
    margin-top: 10px;
  }
}

.users-list__table-btn {
  width: 197px;
}

.users-list__nav,
.users-list__toggle {
  display: flex;
}

.users-list__btn-toggle {
  margin-right: 40px;
  margin-left: 42px;
  height: auto;
  display: flex;
}

.users-list__checkbox {
  padding: 0;
  margin-left: auto;
}

.users-list__nav {
  width: 100%;
  align-items: center;
  justify-content: space-between;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  background-color: $color-default-white;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
}

.users-list__nav-buttons {
  display: flex;
  width: 223px;
  justify-content: space-between;
  margin-right: auto;
  padding-left: 40px;
}

.users-list__map {
  height: 684px;
  width: 100%;
  position: relative;
  border-radius: 0 0 12px 12px;
  overflow: hidden;
  background-color: $color-solitude-2;
}
