.tabs--toggle-list-map {
  .tabs__controls {
    min-width: 150px;
    display: flex;
    align-items: stretch;
    justify-content: center;
    overflow: hidden;
  }

  .tabs__control {
    background-color: $color-default-white;
    color: $color-light-sea-green;
    padding: 6px 15px 8px;
    border: 1px solid $color-light-sea-green;
    margin: 0;
    word-break: break-all;
    font-size: 14px;
    line-height: 18px;
    border-radius: 0;
    display: flex;
    justify-content: space-between;
    flex-wrap: nowrap;

    &:first-child {
      border-radius: 6px 0 0 6px;
    }

    &:last-child {
      border-radius: 0 6px 6px 0;
    }

    &:disabled {
      background-color: $color-solitude-2;
      color: $color-echo-blue;
      border: 1px solid $color-echo-blue;
    }

    transition: color $trans-default, background-color $trans-default;

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover {
        background-color: $color-azure;
      }

      &:active {
        background-color: $color-elm;
        color: $color-default-white;
      }
    }

    &.is-active {
      background-color: $color-light-sea-green;
      color: $color-default-white;
      border: 1px solid $color-light-sea-green;
      pointer-events: none;

      &:focus {
        outline: none;
      }

      @media (hover: hover), screen and (min-width: 0\0) {
        &:hover {
          background-color: $color-light-sea-green;
        }
      }

      &:disabled {
        background-color: $color-echo-blue;
        color: $color-default-white;
        border: 1px solid $color-echo-blue;
      }
    }
  }
}

.tabs--toggle-buy-sell {
  .tabs__controls {
    min-width: 183px;
    display: flex;
    justify-content: space-between;
  }

  .tabs__control {
    margin: 0;
    color: $color-san-marino;
    font-weight: 600;
    background-color: transparent;
    padding: 17px 9px;
    min-width: 76px;
    letter-spacing: 0.5px;
    word-break: break-all;

    transition: color $trans-default, background-color $trans-default;

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover {
        color: $color-light-sea-green-2;
        background-color: $color-azure;
      }
    }

    &.is-active {
      color: $color-light-sea-green-2;
      position: relative;
      pointer-events: none;

      &:focus {
        outline: none;
      }

      &::after {
        content: "";
        display: block;
        width: 100%;
        height: 4px;
        background-color: $color-light-sea-green;
        position: absolute;
        bottom: 0;
        left: 0;
        border-top-left-radius: 6px;
        border-top-right-radius: 6px;
      }
    }
  }
}
