.message {
  margin: 0 auto;
  width: 100%;
  min-height: 630px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.message__icon {
  display: flex;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background-color: $color-default-white;
  margin: 0 auto;
  align-items: center;
  justify-content: center;

  svg {
    width: 60px;
    height: 60px;
  }
}

.message__paragraph {
  margin: 0 auto;
  width: 245px;
  text-align: center;
  line-height: 20px;
  font-weight: 600;
  color: $color-zambezi;
  margin-top: 20px;
  letter-spacing: -0.2px;

  a {
    color: $color-san-marino;
  }
}
