// Colors
// ---------------------------------

// Default

$color-default-black:               #000000;
$color-default-white:               #ffffff;
$color-transparent:                 rgba(255, 255, 255, 0);

// Project palette
// для нейминга цветов используем https://www.htmlcsscolor.com/hex/334482

$color-zambezi:                     #585858;
$color-grey:                        #757575;
$color-san-marino:                  #4b6aa8;
$color-echo-blue:                   #a8b7d4;
$color-solitude:                    #edf0f8;
$color-solitude-2:                  #f2f4f8;
$color-alice-blue:                  #fafbfd;
$color-elm:                         #188780;
$color-light-sea-green:             #10a9a0;
$color-light-sea-green-2:           #2cc2ba;
$color-azure:                       #eefaf9;
$color-burnt-sienna:                #ed7358;
$color-orange:                      #ffa800;
$color-white-smoke:                 #f5f5f5;
$color-charcoal:                    #444444;

// Gradient

$black-to-right:                    linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);

// Typography
// ---------------------------------

// переменная используемая в html для подключения скейлинга
$fz-default:                        16px;
$ff-exo2:                           "exo2", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;
