.user-card {
  width: 220px;
  border-radius: 12px;
  z-index: 20;
  position: relative;
  height: 100%;
  display: flex;
  flex-wrap: wrap;

  fieldset {
    border: 0 none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
  }

  .custom-input {
    width: unset;
    padding: 0;

    input {
      border: none;
      outline: none;
      margin-left: 0;
      padding: 0;
    }
  }

  .custom-input__label {
    text-align: left;
  }

  label {
    display: flex;
    flex-direction: column;
    align-items: baseline;
  }

  &__badges-list {
    list-style: none;
    padding-left: 0;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    width: 100%;
    margin: 0;
    margin-bottom: 14px;
    margin-top: -10px;
    margin-left: -10px;
  }

  &__badges-item {
    margin-top: 10px;
    margin-left: 10px;
  }
}

.user-card__user-name {
  margin: 0;
  display: flex;
  align-items: center;
  font-weight: 700;
  margin-bottom: 14px;

  svg {
    margin-right: 10px;
    width: 20px;
    height: 20px;
  }
}

.user-card__cash-item {
  margin: 0;
  display: flex;
  flex-direction: column;
  margin-bottom: 17px;
  text-align: left;

  &:not(:last-of-type) {
    width: 50%;
  }

  &:not(:nth-child(2n)) {
    text-align: right;
  }
}

.user-card__cash-item:last-of-type {
  margin-bottom: 21px;
  width: 100%;
}

.user-card__cash-label {
  font-size: 12px;
  line-height: 14.4px;
  font-weight: 600;
  color: $color-echo-blue;
  margin-bottom: 4px;
}

.user-card__cash-data {
  line-height: 20px;
  color: $color-zambezi;
}

.user-card__close-btn {
  top: 0;
  right: 0;
  position: absolute;
}

.user-card__change-btn {
  width: 100%;
  min-width: unset;
  margin: 0;
}
