.transaction-info {
  background-color: $color-alice-blue;
  padding: 20px 20px 23px;
  display: flex;
  width: 100%;
}

.transaction-info__item {
  margin: 0;
  display: flex;
  flex-direction: column;
}

.transaction-info__label {
  font-size: 12px;
  line-height: 14.4px;
  font-weight: 600;
  color: $color-echo-blue;
  margin-bottom: 16px;
  letter-spacing: 0.3px;
}

.transaction-info__data {
  font-weight: 400;
  line-height: 20px;
}

.transaction-info__item--name {
  width: 30.5%;

  .transaction-info__data {
    font-weight: 600;
    letter-spacing: 0.3px;
    display: flex;
    align-items: center;
  }

  svg {
    width: 20px;
    height: 20px;
    margin-right: 10px;
  }
}

.transaction-info__item--exchangerate {
  width: 25%;
}

.transaction-info__item--cashlimit {
  width: 46%;
  margin-left: auto;
  line-clamp: 1;
}
