.select {
  width: 480px;

  select {
    width: 480px;
    border-radius: 6px;
    border: 1px solid $color-light-sea-green;
    height: 44px;
    padding-left: 15px;
    color: $color-echo-blue;

    &:focus {
      border: 2px solid $color-default-black;
    }

    option:first-of-type {
      color: $color-echo-blue;
    }

    option {
      height: 44px;
      font-size: 16px;
      line-height: 20px;
      font-weight: 400;
      color: $color-zambezi;
    }

    option:checked {
      color: $color-zambezi;
    }

    option:hover {
      background-color: $color-light-sea-green;
    }
  }

  span {
    font-size: 12px;
    line-height: 14.4px;
    font-weight: 600;
    color: $color-echo-blue;
    letter-spacing: 0.3px;
  }
}
