.custom-toggle {

  display: flex;

  input {
    width: 1px;
    height: 1px;
    z-index: 10;
    top: 4px;
    left: -5px;
    position: absolute;
    clip: rect(0 0 0 0);
    margin: -1px;
    padding: 0;
  }

  label {
    position: relative;

    // cursor: pointer;

    span {
      margin-bottom: 0;
      margin-left: 0;
    }
  }

  // input:disabled ~ label {
  //   pointer-events: none;
  //   cursor: none;
  // }

  .custom-toggle__label {
    letter-spacing: -1.1px;
    padding-left: 50px;
    cursor: pointer;
  }

  .custom-toggle__icon {
    cursor: pointer;
    position: absolute;
    top: 2px;
    left: 0;
    margin-right: 10px;
    z-index: 20;

    width: 42px;
    height: 24px;
    border-radius: 12px;
    border: 1px solid $color-light-sea-green;
    background-color: $color-default-white;

    transition: border $trans-default;

    &:hover {
      background-color: $color-azure;
    }

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 30%;

      width: 16px;
      height: 16px;
      border-radius: 9px;

      background-color: $color-light-sea-green;

      transform: translate(-50%, -50%);

      transition: background-color $trans-default;
    }
  }

  input:checked + .custom-toggle__icon {
    background-color: $color-light-sea-green;

    &::before {
      background-color: $color-default-white;
      right: 30%;
      left: unset;

      transform: translate(50%, -50%);
    }
  }

  input:focus + .custom-toggle__icon {
    border: 2px solid $color-default-black;
  }

  input:disabled:focus + .custom-toggle__icon {
    border: none;
    outline: none;
  }

  input:disabled ~ .custom-toggle__label {
    cursor: none;
    pointer-events: none;
  }

  input:disabled + .custom-toggle__icon {
    background-color: $color-solitude-2;
    border: 1px solid $color-echo-blue;
    cursor: none;
    pointer-events: none;

    &::before {
      background-color: $color-echo-blue;
      pointer-events: none;
    }
  }

  input:checked:disabled + .custom-toggle__icon {
    background-color: $color-echo-blue;
    border: 1px solid $color-echo-blue;
    cursor: none;
    pointer-events: none;

    &::before {
      background-color: $color-default-white;
      pointer-events: none;
    }
  }
}
