.header {
  padding: 12px 0;

  background-color: $color-default-white;

  .container {
    display: flex;
    justify-content: space-between;
  }

  &__logo {
    color: $color-default-white;
    width: 92px;
    height: 36px;

    svg {
      width: 92px;
      height: 36px;
    }
  }

  &--error {
    .user-profile {
      display: none;
    }
  }
}
