.btn {
  display: inline-flex;
  padding: 10px 20px;
  border-radius: 6px;

  color: $color-default-black;
  background-color: $color-default-white;
  border: none;

  cursor: pointer;

  transition: opacity $trans-default;

  &:disabled {
    opacity: 0.5;

    pointer-events: none;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover {
      opacity: 0.8;
    }
  }

  &--greenborder {
    border-radius: 6px;
    border: 1px solid $color-light-sea-green;
    line-height: 20px;
    color: $color-light-sea-green;
    background-color: $color-default-white;
    margin: 0;
    padding: 7px 18px;
    width: 115px;
    word-break: break-all;

    transition: color $trans-default, background-color $trans-default;

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover {
        background-color: $color-light-sea-green;
        color: $color-default-white;
      }

      &:active {
        background-color: $color-elm;
      }

      &:disabled {
        background-color: $color-solitude-2;
        border-color: $color-echo-blue;
        color: $color-echo-blue;
      }
    }
  }

  &--green {
    color: $color-default-white;
    border-radius: 6px;
    background-color: $color-light-sea-green;
    min-width: 260px;
    text-align: center;
    display: block;
    padding: 13px 20px;
    word-break: break-all;

    transition: background-color $trans-default;

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover {
        background-color: $color-light-sea-green-2;
      }

      &:active {
        background-color: $color-elm;
      }

      &:disabled {
        background-color: $color-echo-blue;
        color: $color-default-white;
      }
    }
  }

  &--textblue {
    padding: 0;
    background-color: transparent;
    opacity: 1;
    font-size: 12px;
    line-height: 22px;
    font-weight: 600;
    color: $color-san-marino;
  }
}
