.custom-input {
  position: relative;

  display: flex;
  flex-wrap: wrap;
  width: 220px;
  margin-right: 0;
  padding: 20px 0 24px;
  padding-bottom: 0;

  label {
    display: flex;
    flex-direction: column;
    width: 100%;
    font-size: 12px;
    line-height: 14.4px;
    font-weight: 600;
    color: $color-echo-blue;
    letter-spacing: 0.3px;

    input::placeholder {
      color: $color-echo-blue;
    }

    input {
      flex-grow: 1;
      min-height: 40px;
      margin-left: 0;
      padding: 10px 20px 12px;
      padding-right: 60px;
      margin-top: 6px;
      letter-spacing: 1px;

      font-size: 16px;
      line-height: 20px;
      color: $color-zambezi;

      border: 1px solid $color-light-sea-green;
      border-radius: 5px;

      transition: border $trans-default;

      &:hover {
        border: 1px solid $color-light-sea-green;
      }

      &:active {
        border: 1px solid $color-elm;
      }
    }

    input:disabled {
      color: $color-san-marino;
      background-color: $color-solitude;
      border: 1px solid $color-echo-blue;

      pointer-events: none;

      &:hover {
        color: $color-echo-blue;
        background-color: $color-solitude-2;
        border: 1px solid $color-echo-blue;
      }
    }
  }

  &__unit {
    position: absolute;
    top: 55px;
    right: 20px;
    font-size: 16px;
    font-weight: 400;
  }

  &__btn {
    position: absolute;
    top: 17px;
    right: -2px;
  }

  &__error {
    font-size: 12px;
    line-height: 14px;
    margin-top: 6px;

    color: $color-burnt-sienna;

    opacity: 1;

    transition: opacity $trans-default;
  }

  &:active,
  &:focus {
    .custom-input__btn {
      opacity: 1;
    }
  }
}

.custom-input--wallet,
.custom-input--cardnumber {
  padding-bottom: 6px;

  label {
    input::placeholder {

      color: $color-san-marino;
    }
  }
}
