.users-nav {
  padding-left: 40px;
  padding-right: 40px;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  background-color: $color-default-white;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
}

.users-nav,
.users-nav__tabs {
  display: flex;
}

.users-nav__custom-toggle {
  padding: 0;
  margin-left: auto;
  margin-right: 40px;
}
